.item{
    width: 21%;
    padding: 16px;
}
.item img{
    width: 100%;
    object-fit: cover;
    border-radius: 8px;
    transition: opacity ease-in-out;
}
.item p{
    margin: 6px 0px;
}
.item-prices{
    display: flex;
    gap: 15px;
}
.item-price-new{
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}
.item-price-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}
.item-rating {
    color: #FFD700; /* Gold color for stars */
    font-size: 18px; /* Adjust size as needed */
  }
  

@media(max-width:800px){
    .item{
        width: 35%;
    }
    .item-price-old{
        font-size: 12px;
    }
}
@media(max-width:500px){
    .item p{
        font-size: 14px;
    }
    .item-price-new{
        font-size: 14px;
    }
}



.item:hover {
    transform: scale(1.15);
    transition: 1s;
}

