.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    padding-left: 30px;
    padding-top: 180px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7); /* Dark overlay */
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    z-index: 1000;
}

.popup-content {
    background: white;
    padding: 30px;
    border-radius: 10px;
    text-align: center;
    position: relative;
    width: 300px; /* Adjust width */
    height: auto;
    max-width: 90%;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
    animation: slideIn 1s ease-in-out; /* Slower animation */
}

.close-btn {
    position: absolute;
    top: 10px;
    right: 15px;
    border: none;
    background: none;
    font-size: 22px;
    cursor: pointer;
    color: #333;
}

.shop-now {
    background: #ff4141;
    color: white;
    border: none;
    padding: 12px 20px;
    margin-top: 15px;
    cursor: pointer;
    border-radius: 5px;
    font-size: 16px;
}

/* Slide-in animation from the left */
@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateX(-100%); /* Start from outside the left */
    }
    to {
        opacity: 1;
        transform: translateX(0); /* Move to the final position */
    }
}