* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  overflow-x: hidden;
  width: 100%;
}

.footer {
  background-color: #111;
  color: white;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  text-align: left;
  width: 100%;
  max-width: 100%;
  padding: 50px 50px; /* Adjust padding to ensure proper spacing */
}

.footer-section {
  flex: 1 1 220px; /* Ensuring sections have enough width */
  margin-left: 30px; /* Moves content inward from the left */
  min-width: 220px;
}

.footer-section h3 {
  font-size: 16px;
  margin-bottom: 15px;
  font-weight: bold;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  font-size: 14px;
  margin-bottom: 8px;
  cursor: pointer;
  transition: color 0.3s ease;
}

.footer-section ul li:hover {
  color: #aaa;
}

.footer-bottom {
  width: 100%;
  text-align: center;
  margin-top: 20px;
  padding-top: 15px;
  border-top: 1px solid #444;
  font-size: 14px;
}

/* Responsive Design */

/* For tablets (max-width: 1024px) */
@media (max-width: 1024px) {
  .footer {
      padding: 30px 20px;
      flex-wrap: wrap;
      justify-content: center;
  }

  .footer-section {
      flex: 1 1 45%; /* Two sections per row */
      margin-bottom: 20px;
  }
}

/* For mobile devices (max-width: 768px) */
@media (max-width: 768px) {
  .footer {
      flex-direction: column;
      padding: 20px;
      text-align: center;
  }

  .footer-section {
      width: 100%;
      margin: 0 auto 20px;
      text-align: center;
  }

  .footer-section h3 {
      font-size: 18px;
  }

  .footer-section ul li {
      font-size: 16px;
  }
}
