.productdisplay{
    display: flex;
    padding: 0px 10%;
    gap: 40px;
    justify-content: space-between;
}
.productdisplay-left{
    display: flex;
    gap: 18px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.productdisplay-img-list img{
    height: 110px;
}
.productdisplay-main-img {
    width: 100%;
    height: 1120px;
    transition: transform 0.3s ease-in-out;
    cursor: crosshair;
}

.productdisplay-img {
    position: relative;
    overflow: hidden;
}

.zoom-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: 200%;
    pointer-events: none;
    transition: opacity 0.3s ease-in-out;
    opacity: 1;
}

.productdisplay-right{
    display: flex;
    flex-direction: column;
}
.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 30px;
    font-weight: 700;
}
.productdisplay-right-stars{
    display: flex;
    align-items: center;
    margin-top: 10px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 14px;
}
.productdisplay-right-prices{
    display: flex;
    margin: 25px 0px;
    gap: 20px;
    font-size: 22px;
    font-weight: 700;
}
.productdisplay-right-price-old{
    color: #818181;
    text-decoration: line-through;
}
.productdisplay-right-price-new{
    color: #ff4141;
}
.productdisplay-right-size h1{
    margin-top: 30px;
    color: #656565;
    font-size: 18px;
    font-weight: 600;
}
.productdisplay-right-sizes{
    display: flex;
    margin: 20px 0px;
    gap: 15px;
}
.productdisplay-right-sizes div{
    padding: 16px 20px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}
.productdisplay-right button{
    padding: 10px 20px;
    width: 180px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background: #FF4141;
    margin-bottom: 25px;
    border: none;
    outline: none;
    cursor: pointer;
}
.productdisplay-right-category{
    margin-top: 10px;
}
.productdisplay-right-category span{
    font-weight: 600;
}

/* ----- media query------- */


@media(max-width:1024px){
    .productdisplay{
        padding: 0px 8%;
    }
    .productdisplay-img-list{
        gap: 10px;
    }
    .productdisplay-img-list img{
        height: 90px;
    }
    .productdisplay-main-img{
        height: 400px;
    }
    
}
@media(max-width:900px){
    .productdisplay{
        gap: 20px;
    }
    .productdisplay-left{
        gap: 10px;
    }
    .productdisplay-img-list{
        gap: 10px;
    }
    .productdisplay-img-list img{
        height: 80px;
    }
    .productdisplay-main-img{
        height: 350px;
    }
    
}
@media(max-width:800px){
    .productdisplay{
        flex-wrap: wrap;
    }
    .productdisplay-left{
        width: 100%;
        justify-content: center;
    }
}
@media(max-width:500px){
    .productdisplay-right h1 {
        font-size: 20px;
    }
    .productdisplay-right-prices {
        margin: 15px 0;
        font-size: 18px;
    }
    .productdisplay-right-size h1{
        margin-top: 15px;
    }
    .productdisplay-right-size h1{
        margin: 10px 0;
    }
    .productdisplay-right-size div{
        font-size: 14px;
    }
}
.productdisplay-right-country {
    margin-top: 20px;
}

.productdisplay-right-country label {
    font-size: 16px;
    font-weight: bold;
    margin-right: 10px;
    color: #333;
}

.productdisplay-right-country select {
    padding: 8px 12px;
    font-size: 16px;
    border: 2px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    cursor: pointer;
    transition: border-color 0.3s ease;
}

.productdisplay-right-country select:hover {
    border-color: #ff6600;
}

.productdisplay-right-country select:focus {
    border-color: #ff6600;
    outline: none;
}
.button_alignment {
    display: flex;
    justify-content: left;
    gap: 15px;
    flex-wrap: wrap;
    margin-top: 20px;
}

.button_alignment button {
    padding: 17px 25px;
    font-size: 14px;
    font-weight: 550;
    color: white;
    background: #FF4141;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 2px;
    transition: background 0.3s ease, transform 0.2s ease;
}

.button_alignment button:hover {
    background: #d93636;
    transform: scale(1.05);
}

.button_alignment button:active {
    transform: scale(0.98);
}

/* Responsive Styling */
@media (max-width: 600px) {
    .button_alignment {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .button_alignment button {
        width: 90%;
    }
}
.info-box{
    display:flex;
    border:1px solid brown;
    justify-content: space-between;
    margin-top: 30px;
    padding:20px;
    border-radius:5px
}
.info-boxs{
    display:flex;
    border:1px solid blue;
    justify-content: space-between;
    margin-top: 30px;
    padding:15px;
    border-radius:5px
}
.image{
    width: 100px;
}
.image_2{
    width: 100px;
}
.image_3{
    margin-top: 5px;
    width: 100px;
    margin-bottom: 30px;
}
.icons{
    display: flex;
    flex-direction: row;
    gap: 180px;
    
}