.hero {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
}

.hero-image {
    width: 100%;
    height: 800px;
    margin-bottom: 60px;
}
/* Customize Swiper Navigation Arrows */
.swiper-button-prev,
.swiper-button-next {
    color:black !important; /* Change arrow color */
    font-weight: bold;
}

/* Customize Swiper Pagination Dots */
.swiper-pagination-bullet {
    background: black !important; /* Change dot color */
    opacity: 1;
}

.swiper-pagination-bullet-active {
    background: grey !important; /* Change active dot color */
}